// components/AddTeamModal.js
import React, { useState } from 'react';
import styles from '../styles/AddTeamModal.module.css'; // Import CSS module

const AddTeamModal = ({ onClose }) => {
  const [teamName, setTeamName] = useState('');
  const [teamLogo, setTeamLogo] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await fetch('/api/add-team', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ teamName, teamLogo }),
      });

      if (response.ok) {
        alert('Team added successfully');
        onClose();
      } else {
        alert('Failed to add team');
      }
    } catch (error) {
      console.error('Error adding team:', error);
      alert('An error occurred while adding the team.');
    }
  };

  return (
    <div className={`modal fade show ${styles.modalDialog}`} id="addTeamModal" tabIndex="-1" aria-labelledby="addTeamModalLabel" aria-hidden="true" style={{ display: 'block' }}>
      <div className="modal-dialog">
        <div className={`modal-content ${styles.modalContent}`}>
          <div className="modal-header">
            <h5 className="modal-title" id="addTeamModalLabel">Add New Team</h5>
            <button type="button" className="btn-close" aria-label="Close" onClick={onClose}></button>
          </div>
          <div className="modal-body">
            <form id="addTeamForm" onSubmit={handleSubmit}>
              <div className="mb-3">
                <label htmlFor="teamName" className="form-label">Team Name</label>
                <input
                  type="text"
                  id="teamName"
                  className="form-control"
                  placeholder="Enter Team Name"
                  value={teamName}
                  onChange={(e) => setTeamName(e.target.value)}
                  required
                />
              </div>
              <div className="mb-3">
                <label htmlFor="teamLogo" className="form-label">Team Logo (URL)</label>
                <input
                  type="text"
                  id="teamLogo"
                  className="form-control"
                  placeholder="Enter URL of Team Logo"
                  value={teamLogo}
                  onChange={(e) => setTeamLogo(e.target.value)}
                  required
                />
              </div>
              <div className="text-center">
                <button type="submit" id="submitTeamBtn" className="btn btn-success">Submit</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AddTeamModal;